import Form from "./Form/Form";
import HorbContext from "./store/HorbContext";
import { useContext, useState, useEffect } from "react";
import LogList from "./Log/LogList";
import Button from "./UI/Button";
import Graph from "./Analytics/Graph";
import GraphContextProvider from "./Analytics/GraphContextProvider";

const Main = () => {
  const [toggleForm, setToggleForm] = useState(false);
  const [toggleLog, setToggleLog] = useState(false);
  const HorbCtx = useContext(HorbContext);

  function customFormHandler() {
    setToggleForm(true);
  }
  function customLogHandler() {
    setToggleLog(true);
  }

  //   if (horbCtx.routeList) {

  //   }

  useEffect(() => {
    HorbCtx.getRouteList();
    console.log("Data fetched!");
  }, []);

  return (
    <>
      {toggleForm && <Form submitHandler={setToggleForm} />}
      {!toggleForm && (
        <Button size="normal" color="blue" align="center" onClickHandler={customFormHandler}>
          New Entry
        </Button>
      )}
      {toggleLog && HorbCtx.routeList && <LogList />}
      {!toggleLog && (
        <Button size="normal" color="blue" align="center" onClickHandler={customLogHandler}>
          Show Logs
        </Button>
      )}
      {HorbCtx.routeList && (
        <GraphContextProvider>
          <Graph data={HorbCtx.routeList} yLabel="Minutes taken" xLabel="May - Days" />
        </GraphContextProvider>
      )}
    </>
  );
};

export default Main;
